body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
    text-align: center;
}
textarea{
    width: 80vw;
    height: 40vh;
    max-width: 500px;
    max-height: 200px;
}

.container{
    margin-top: 4em;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.content{

    width: 80vw;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    max-width: 500px;
    text-align: center;
    margin-top: 3em;
}

.footer{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 100vw;
    margin-top: 5em;
}

.button{
    padding: 0px;
    margin: 0px;

}

button{
    margin: 10px;
}
